@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
}
@font-face {
  font-family: "SintonyRegular";
  src: url("../fonts/Sintony-Regular.woff2") format("woff2");
  font-weight: 400;
}
@font-face {
  font-family: "SintonyBold";
  src: url("../fonts/Sintony-Bold.woff2") format("woff2");
  font-weight: 700;
}
@font-face {
  font-family: "EuroCaps";
  src: url("../fonts/EUROCAPS.woff2") format("woff2");
  font-weight: 400;
}
.c-select {
  width: 100%;
}
.c-select .dropdown .dropdown-toggle,
.c-select .dropdown .selected-items {
  font-family: "SintonyRegular", sans-serif;
}
.c-select .dropdown .btn-outline-dark {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
}
.c-select .dropdown .btn-outline-dark:active,
.c-select .dropdown .btn-outline-dark:focus,
.c-select .dropdown .btn-outline-dark {
  color: #212529;
}
.c-select .dropdown .btn-outline-dark:hover {
  background-color: #6c757d !important;
  color: #f8f9fa;
}
.c-select .dropdown .dropdown-menu {
  border-color: #adb5bd;
  background-color: #FBFBFB;
}
.c-select .dropdown .dropdown-menu .dropdown-item {
  cursor: pointer;
}
.c-select .dropdown .dropdown-menu .dropdown-item:hover {
  background-color: #0d6efd;
  color: #fff;
}
.c-select .dropdown .dropdown-menu .hidden {
  display: none;
}
.c-select .dropdown .dropdown-menu .dropdown-item,
.c-select .dropdown .dropdown-menu .form-control::placeholder {
  font-family: "SintonyRegular", sans-serif;
  letter-spacing: 2px;
}
.c-select .dropdown .dropdown-menu .form-control:focus {
  border-color: #dee2e6;
}
.c-select .selected-items {
  min-height: 2.125em;
}
.c-select .selected-items > div {
  background-color: #0dcaf0;
  border-radius: 5px;
}
.c-select .selected-items > div .btn-close {
  padding: 0;
  width: 0.65em;
  height: 0.65em;
}
.c-select .selected-items > div .btn-close:focus {
  box-shadow: none;
}

.c-dropdown {
  width: 100%;
}
.c-dropdown .dropdown .dropdown-menu {
  border-color: #adb5bd;
}
.c-dropdown .dropdown .dropdown-menu .dropdown-item {
  cursor: pointer;
}
.c-dropdown .dropdown .dropdown-menu .dropdown-item:hover {
  background-color: #0d6efd;
  color: #fff;
}
.c-dropdown .selected-items > div {
  background-color: #0dcaf0;
  border-radius: 5px;
}
.c-dropdown .dropdown-item,
.c-dropdown input::placeholder {
  font-family: "SintonyRegular", sans-serif;
  letter-spacing: 2px;
}

.c-form {
  background-color: #FBFBFB;
  font-family: "SintonyBold", sans-serif;
  letter-spacing: 2px;
}
.c-form select,
.c-form option {
  font-family: "SintonyRegular", sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
}
.c-form .btn-violet {
  font-family: "SintonyBold", sans-serif;
  letter-spacing: 4px;
}

.header {
  background-color: #370665;
  height: 96px;
  max-height: 96px;
}
.header .navbar-toggler {
  background-color: #FC9918;
}
.header .navbar {
  background-color: #370665;
}
.header .logo {
  font-family: "EuroCaps";
  font-size: 56px;
  color: #F14A16;
}
.header .menu-item {
  position: relative;
}
.header .menu-item::after {
  position: absolute;
  bottom: -1px;
  left: 50%;
  content: "";
  width: 0px;
  opacity: 0;
  height: 3px;
  background-color: #FC9918;
  transition-property: opacity, width, left;
  transition-duration: 200ms;
  transition-timing-function: linear;
}
.header .menu-item:hover::after {
  color: #FC9918;
  opacity: 1;
  left: 0;
  width: 100%;
}
.header .menu-link {
  color: #FBFBFB;
}
.header .menu-link, .header .menu-link.active {
  font-family: "SintonyBold";
  font-size: 22px;
}
.header .menu-link:hover, .header .menu-link.active {
  color: #FC9918;
}

.body-wrapper {
  height: 100vh;
}

.wrapper {
  font-family: "SintonyRegular", sans-serif;
  font-weight: 400;
  height: 100vh;
  background-color: #35589A;
}

a {
  color: inherit;
}

button {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  letter-spacing: inherit;
}

input {
  outline: 0;
}

.cnt {
  max-width: 1280px;
  margin: 0 auto;
}